<template>
  <div class="font-size-container">
    <!-- 顶部标题栏 -->
    <div class="header">
      <div class="back-button" @click="goBack">
        <img src="../assets/back.png" alt="返回" class="back-img" />
      </div>
      <div class="title">设置字体大小</div>
    </div>

    <!-- 聊天内容区域 -->
    <div class="chat-container">
      <!-- 老人消息 -->
      <div class="message-row right">
        <div class="message-content">
          <div class="message-bubble">
            <span class="old-message" :style="{ fontSize: currentFontSize }">预览字体大小</span>
          </div>
        </div>
        <div class="avatar">
          <img src="../assets/avatar.png" alt="老人头像" />
        </div>
      </div>

      <!-- 医生消息 -->
      <div class="message-row left">
        <div class="avatar">
          <img src="../assets/doctor.png" alt="医生头像" />
        </div>
        <div class="message-content">
          <div class="message-bubble">
            <span class="doctor-message" :style="{ fontSize: currentFontSize }">拖动下面的滑块，可设置字体大小</span>
          </div>
        </div>
      </div>

      <!-- 医生消息 -->
      <div class="message-row left">
        <div class="avatar1">
          <img src="../assets/doctor.png" alt="医生头像" />
        </div>
        <div class="message-content">
          <div class="message-bubble">
            <span class="doctor-message" :style="{ fontSize: currentFontSize }">设置后，会改变小程序界面中的字体大小，如果在使用过程中存在问题和建议，可反馈给小程序团队。</span>
          </div>
        </div>
      </div>
    </div>

    <!-- 字体大小调节 -->
    <div class="font-size-control">
      <div class="font-size-title">字体大小</div>
      <div class="font-slider-container">
        <div class="small-icon">
          <span class="a-text">A</span>
        </div>
        <div class="slider-track">
          <div class="slider-lines">
            <div class="slider-line" v-for="i in 6" :key="i" @click="setFontSize(i)"></div>
          </div>
          <div class="slider-thumb" :style="{left: thumbPosition + '%'}" 
                     @mousedown="startDrag"
                     @touchstart="startDrag">
                  <div class="size-label" :style="{ fontSize: labelFontSize + 'px' }">{{ sizeLabels[currentSize-1] }}</div>
                </div>
        </div>
        <div class="large-icon">
          <span class="A-text">A</span>
        </div>
      </div>
      <div class="size-label center">标准</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FontSize',
  data() {
    return {
      fromReportView: false,
      currentSize: 3, // 默认字体大小级别 (1-6)
      isDragging: false,
      sizeLabels: ['极小', '小', '标准', '大', '较大', '超大'],
         // 新增：6个级别的字体大小（单位：px）
    fontSizes: [14, 16, 20, 24, 28, 32]
    }
  },
  computed: {
    thumbPosition() {
      // 将字体大小级别转换为百分比位置
      return (this.currentSize - 1) * 20;
    },
    labelFontSize() {
      // 根据当前级别设置标签字体大小
      const fontSizes = [12, 16, 20, 24, 28, 32]; // 对应六个级别的字体大小
      return fontSizes[this.currentSize - 1];
    },
     // 新增：当前聊天消息的字体大小
  currentFontSize() {
    return `${this.fontSizes[this.currentSize - 1]}px`;
  }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
  },
  mounted() {
    // 添加全局事件监听
    window.addEventListener('mousemove', this.onDrag);
    window.addEventListener('mouseup', this.stopDrag);
    // 添加触摸事件支持
    window.addEventListener('touchmove', this.onTouchDrag);
    window.addEventListener('touchend', this.stopDrag);
  },
  beforeUnmount() {
    // 移除全局事件监听
    window.removeEventListener('mousemove', this.onDrag);
    window.removeEventListener('mouseup', this.stopDrag);
    // 移除触摸事件
    window.removeEventListener('touchmove', this.onTouchDrag);
    window.removeEventListener('touchend', this.stopDrag);
  },
  methods: {
    goBack() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    startDrag(e) {
      this.isDragging = true;
      // 立即更新位置，避免第一次点击时的延迟
      if (e.type === 'touchstart') {
        this.onTouchDrag(e);
      } else {
        this.onDrag(e);
      }
    },
    stopDrag() {
      this.isDragging = false;
    },
    onDrag(e) {
      if (!this.isDragging) return;
      
      // 获取滑块轨道的位置和宽度
      const track = document.querySelector('.slider-track');
      const rect = track.getBoundingClientRect();
      
      // 计算鼠标在轨道上的相对位置
      let position = (e.clientX - rect.left) / rect.width;
      position = Math.max(0, Math.min(1, position));
      
      // 连续滑动效果：直接使用position * 5 + 1计算当前大小
      // 这样可以实现平滑的拖动效果，而不是跳跃式的
      this.currentSize = Math.max(1, Math.min(6, Math.round(position * 5) + 1));
      if (this.currentSize < 1) this.currentSize = 1;
      if (this.currentSize > 6) this.currentSize = 6;
    },
    // 添加触摸拖动处理方法
    onTouchDrag(e) {
      if (!this.isDragging) return;
      
      // 阻止页面滚动
      e.preventDefault();
      
      // 获取滑块轨道的位置和宽度
      const track = document.querySelector('.slider-track');
      const rect = track.getBoundingClientRect();
      
      // 获取触摸位置
      const touch = e.touches[0] || e.changedTouches[0];
      
      // 计算触摸在轨道上的相对位置
      let position = (touch.clientX - rect.left) / rect.width;
      position = Math.max(0, Math.min(1, position));
      
      // 连续滑动效果：使用Math.round确保平滑过渡
      this.currentSize = Math.max(1, Math.min(6, Math.round(position * 5) + 1));
    },
    setFontSize(level) {
      this.currentSize = level;
    }
  }
}
</script>

<style scoped>
