}

.slider-thumb {
  width: 30px;
  height: 30px;
  opacity: 1;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  cursor: grab;
  transition: left 0.05s linear; /* 减少过渡时间，使拖动更加灵敏 */
  touch-action: none; /* 防止触摸设备上的默认行为干扰拖动 */
}

.slider-thumb:active {
  cursor: grabbing;
  box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, 0.6);
}

/* 增加滑块轨道的可点击区域 */
.slider-track {
  flex: 1;
  height: 20px; /* 增加高度使其更容易点击 */
  background-color: transparent; /* 背景透明 */
  margin: 0 15px;
  position: relative;
  width: 328.69px;
  margin-top: 40px;
  cursor: pointer; /* 添加指针样式 */
}

.slider-track::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  background-color: rgba(229, 229, 229, 1);
  left: 0;
